* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  --text-color: #000;
  --highlight-color: #00acdc;
  --background-color: #fff;
  text-transform: uppercase;
  color: var(--text-color);
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.header {
  background-color: var(--background-color);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

a > .header__logo {
  color: var(--text-color);
  width: 40px;
  height: 40px;
}

.nav__list {
  justify-content: center;
  align-items: center;
  width: fit-content;
  list-style: none;
  display: flex;
}

.nav__item {
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  display: flex;
  position: relative;
}

.nav__link {
  color: var(--text-color);
  size: 12px;
  place-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__item > .nav__link:active {
  color: var(--highlight-color);
}

.nav__list .nav__item:last-child {
  margin-right: 0;
}

.nav__item > .nav__link.is-active {
  color: var(--highlight-color);
}

.nav__link.is-active:after {
  content: "";
  background: var(--highlight-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
